.download {
    margin-top: 200px;
    padding: 157px 134px;
    display: flex;
    align-items: center;
    background: linear-gradient(90deg, #A6F2F2 0%, #DDF181 100%);
    &__add {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        & h2 {
            max-width: 619px;
        }
    }
    &__app {
        display: flex;
        align-items: center;
        margin-top: 60px;
        & a {
            &:nth-child(1){
                margin-right: 27px;
            }
        }
    }
    &__img {
        width: 50%;
    }
}