.reviews {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    &::before {
        content: '';
        width: 172px;
        height: 172px;
        border-radius: 50%;
        background: #9CD9EC;
        filter: blur(100px);
        position: absolute;
        top: 16%;
        left: 6%;
    }
    & h2 {
        margin-top: 200px;
        max-width: 710px;
        text-align: center;
    }
    &__content {
        margin-top: 100px;
        max-width: 690px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: $white;
        box-shadow: 0px 20px 30px rgba(227, 202, 202, 0.3);
        border-top: 12px solid $orange;
        padding: 0 60px;
        position: relative;
        & p {
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 24px;
            line-height: 36px;
            color: $wheat;
            max-width: 570px;
            margin: 50px 0;
        }
        &::after {
            content: '';
            width: 242px;
            height: 242px;
            border-radius: 50%;
            background: #FFF8F8;
            position: absolute;
            bottom: -22%;
            right: -18%;
            z-index: -1;
        }
    }
    &__people {
        margin-top: 58px;
        display: flex;
        align-items: center;
        & img {
            border-radius: 50%;
            margin-right: 20px;
        }
        & h3 {
            font-weight: bold;
            font-size: 24px;
            line-height: 33px;
            color: $black2;
            margin: 0;
        }
        & span {
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: $orange;
            margin-top: 12px;
        }
    }
}