.destination {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 259px;
    &::before {
        content: '';
        width: 172px;
        height: 172px;
        border-radius: 50%;
        background: $green2;
        filter: blur(100px);
        position: absolute;
        top: -230px;
        right: 54px;
    }
    &__list {
        display: flex;
        justify-content: center;
        margin-top: 299px;
    }
    &__item {
        position: relative;
        &-up {
            position: relative;
            transform: translateY(-200px);
            &::before {
                content: '';
                position: absolute;
                background: url('../img/Vector.svg');
                width: 169px;
                height: 169px;
                top: 0;
                left: -44%;
            }
            &::after {
                content: '';
                position: absolute;
                background: url('../img/Vector2.svg');
                width: 148px;
                height: 169px;
                top: 0;
                right: -40%;
            }
        }
        &:not(:last-child) {
            margin-right: 30px;
        }
    }
    &__place {
        display: flex;
        align-items: center;
        position: absolute;
        bottom: -9%;
        left: 12%;
        width: 400px;
        height: 105px;
        background: $white;
        box-shadow: 0px 10px 25px rgba(215, 190, 190, 0.2);
        padding: 20px 32px;
        & h3 {
            margin: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 33px;
            color: $black2;
        }
        & button {
            width: 50px;
            height: 50px;
            border: none;
            background: $orange;
            font-weight: bold;
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-size: 24px;
            line-height: 29px;
            color: $white;
            text-align: center;
            cursor: pointer;
            margin-left: 20px;
            &:hover {
                background: $orange2;
            }
        }
    }
    &__reviews {
        display: flex;
        align-items: center;
        margin-top: 17px;
        & p {
            margin: 0;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: $orange;
            margin-right: 30px;
        }
        & span {
            margin-left: 7px;
            font-family: "Lexend Deca", "Tahoma", sans-serif;
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            color: $wheat;
        }
    }
    & a {
        transform: translateY(-11px);
    }
}