.customers-photo {
    & img {
        border-radius: 50%;
        position: absolute;
        &:nth-child(1) {

            border: 5px solid #C549FF;
            box-shadow: 0px 10px 30px #EFD8FA;
            top: 50%;
            left: 10%;
        }
        &:nth-child(2) {
            border: 5px solid #FFCA45;
            box-shadow: 0px 10px 30px rgba(253, 216, 122, 0.25);
            top: 70%;
            left: 5%;
        }
        &:nth-child(3) { 
            border: 7px solid #FF4567;
            box-shadow: 0px 10px 30px #F0CBD2;
            top: 70%;
            left: 15%;
        }
        &:nth-child(4) {
            border: 6px solid #5F45FF;
            box-shadow: 0px 10px 30px #D2CDF4;
            bottom: 10%;
            left: 9%;
        }
        &:nth-child(5) {
            border: 7px solid #18A0FB;
            box-shadow: 0px 10px 30px #C3E2F7;
            top: 50%;
            right: 7%;
        }
        &:nth-child(6) {
            border: 6px solid #FFCA45;
            box-shadow: 0px 10px 30px rgba(253, 216, 122, 0.25);
            top: 70%;
            right: 15%;
        }
        &:nth-child(7) {
            border: 6px solid #65CE54;
            box-shadow: 0px 10px 30px #D4EAD0;
            bottom: 9%;
            right: 10%;
        }
    }
    
}