.slider-button {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    & button {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: $grey2;
        border: none;
        cursor: pointer;
        padding: 0;
        &:not(:last-child) {
            margin-right: 20px;
        }
        &.slider-button-active,
        &:hover  {
            background: $orange;
            position: relative;
            &::before {
                content: '';
                width: 22px;
                height: 22px;
                border-radius: 50%;
                border: 2px solid $orange;
                background: transparent;
                position: absolute;
                top: -8px;
                left: -8px;
            }
        }
    }
}